/* **********************************************************************************/
/* *********//*/ ROOT /*/
/* ***********************************************************************************/

:root {
 --primary-red: hsla(8, 72%, 52%, 1);
 --primary-red04: hsla(8, 72%, 52%, 0.4);
 --primary-yellow: hsla(76, 98%, 50%, 1);
 --primary-blue: hsla(180, 100%, 50%, 1);
 --primary-blue04: hsla(180, 100%, 50%, 0.4);
 --neutral1: hsla(127, 4%, 60%, 1);
 --neutral04: hsla(127, 4%, 60%, 0.4);
 --white1: hsla(100, 100%, 100%, 1.0);
 --white08: hsla(100, 100%, 100%, 0.8);
 --white04: hsla(100, 100%, 100%, 0.4);
 --white02: hsla(100, 100%, 100%, 0.2);
 --black1: hsla(0, 0%, 0%, 1.0);
 --black02: hsla(0, 0%, 0%, 0.2);
 --black03: hsla(0, 0%, 0%, 0.3);
 --black04: hsla(0, 0%, 0%, 0.4);
 --black08: hsla(0, 0%, 0%, 0.8);
 --cssui-animation-rotate: rotate(-180deg);
 --cssui-animation-timing: .25s ease-in-out;
 --cssui-font-family: cy,-apple-system, blinkmacsystemfont, "Segoe UI", roboto, "Helvetica Neue", arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
 --cssui-padding: 0.5rem;
 --cssui-radius: 8px;
}

/* **********************************************************************************/
/* *********//*/ CSS RESETS /*/
/* ***********************************************************************************/

html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strong,sub,sup,var,
b,u,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,
aside,canvas,details,embed,figure,figcaption,footer,header,nav,output,ruby,section,
summary,time,mark,audio,video { margin: 0; padding: 0; border: 0; font: inherit; vertical-align: baseline; }

article,aside,details,figcaption,figure,footer,header,nav,section { display: block; }

* { box-sizing: border-box; }
input[type="radio"] { display: none; place-content: center; -webkit-appearance: none;appearance: none; }
html { font-size: 100%; color: var(--black1); overflow: hidden;} /*16px*/
body { height:  100vh; display:  flex; justify-content: center; align-items: center; line-height: 1.2; font-family: var(--cssui-font-family); }
blockquote,q { quotes: none; }
ol,ul { list-style: none; }
canvas { position: absolute; top: 0; left: 0; z-index: 1000; }
 /* **********************************************************************************/
 /* *********//*/ Styles to refactor /*/
 /* ***********************************************************************************/


 p,h2, h3, h4, h5 { font-family: cy,sans-serif; font-weight: 300;font-style: normal; margin: 0; }
 h1, h2 { font-family: "Orbitron", sans-serif; font-weight: 900; margin: 0 auto; }

 /* ****************************** /* DROPDOWN */
 [data-dropdown] {
   --dropdown-border-radius: var(--cssui-radius);
   --border-color: var(--white04);
   --dropdown-link-background: var(--white02);
   --drodpwon-link-color: var(--primary-red04);
   --dropdown-panel-background: var(--black02);
   --dropdown-padding: var(--cssui-padding);
   width: 8vw;
   border-radius: var(--cssui-radius);
   font-size: 0.77rem;
   color: var(--white1);
 }

 [data-dropdown] summary {
   position: relative;
   padding: var(--dropdown-padding);
 }

 [data-dropdown] summary:hover {
   cursor: pointer;
 }

 [data-dropdown] summary::-webkit-details-marker {
   display: none; 
 }
   
 [data-dropdown] summary:focus {
   outline-width: 1px;
 }


 [data-dropdown] ul {
   margin: 0;
   padding-left: 10px;
   background: var(--dropdown-panel-background);
   list-style: none;
 }

 [data-dropdown] li {
   margin: 0;
   padding: 0;
   border-top: var(--dropdown-border, 0) solid var(--dropdown-border-color);
 }

 [data-dropdown] li > a {
   display: block;
   padding: var(--dropdown-padding);
   color: var(--dropdown-link-color);
   text-decoration: none;
 }

 [data-dropdown] li > a:hover {
   background-color: var(--dropdown-link-background);
 }
 /* ****************************** /* ACORDION */


 [data-modal] {
   --modal-background: var(--white08);
   --modal-max-size: 40vw;
   --modal-overlay-background: var(--black08);
   --modal-radius: 4px;
   --modal-size: 80%;
   --modal-spacing: 1rem;

   visibility: hidden;
   opacity: 0;
   transition: opacity .8s ease-in-out;
   position: fixed;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   display: flex;
   align-items: center;
   justify-content: center;
 }

 [data-modal-overlay] {
   position: fixed;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   background-color: var(--modal-overlay-background);
 }

 [data-modal-dialog] {
   z-index: var(--modal-level);
   width: var(--modal-size);
   max-width: var(--modal-max-size);
   padding: var(--modal-spacing);
   border-radius: var(--modal-radius);
   background-color: var(--modal-background);
   opacity: 0;
   transform: translateY(-1rem);
   transition: opacity .3s ease, transform .3s ease;
   transition-delay: .2s;
 }

 [data-modal]:target {
   visibility: visible;
   opacity: 1;
   z-index: 60000;
 }

 [data-modal]:target [data-modal-dialog] {
   opacity: 1;
   transform: translateY(0);
 }

 [data-modal-close] {
   position: absolute;
   top: var(--modal-spacing);
   right: var(--modal-spacing);
   color: var(--cssui-gray-darkest);
 }



 /* ****************************** /* Tool Tip */

 [data-tooltip] {
   --tooltip-background: var(--cssui-gray-darkest);
   --tooltip-padding: 4px 6px;
   --tooltip-text-color: var(--cssui-gray-light);
   --tooltip-fontsize: .75rem;
   /* --tooltip-radius: 0px */

   position: relative;
 }

 [data-tooltip]::after,
 [data-tooltip]::before {
   opacity: 0;
   transition: opacity .3s ease;
 }

 [data-tooltip]::after {
   position: absolute;
   bottom: calc(100% + 2px);
   left: 50%;
   content: attr(title);
   display: inline-block;
   width: auto;
   padding: var(--tooltip-padding);
   background-color: var(--tooltip-background);
   color: var(--tooltip-text-color);
   font-size: var(--tooltip-fontsize);
   white-space: nowrap;
   transform: translateX(-50%);
   border-radius: var(--tooltip-radius, 0);
 }

 [data-tooltip]:hover::after,
 [data-tooltip]:hover::before,
 [data-tooltip]:focus::after,
 [data-tooltip]:focus::before {
   opacity: 1;
 }

 [data-tooltip]::before {
   position: absolute;
   bottom: calc(100% - 2px);
   left: 50%;
   content: '';
  width: 0;
  height: 0;
  border-top: 5px solid var(--tooltip-background);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
   transform: translateX(-50%);
 }


 /* **********************************************************************************/
 /* *********//*/ POSITIONS & Z-INDEX /*/
 /* ***********************************************************************************/

 .foggy { position: absolute; top: 10%; right:  40%; z-index: 1000; }

 /* **********************************************************************************/
 /* *********//*/ Foggy FX/*/
 /* ***********************************************************************************/

 .foggy {
   width: 800px;
   height: 200px;
   user-select: none;
 }

 .fog-cloud {
   opacity: 0;
   width: 400px;
   border-radius: 100%;
   height: 200px;
   position: relative;
   left: 10%; right: 5%; 
   margin: auto;
   user-select: none;
   pointer-events: none;
 }


 @media only screen  {
   /* *********//*/ logo  /*/
   .header-info { display:  flex; flex-direction: column; align-items: flex-end; position: fixed; z-index: 50000; top: 2%; right: 2%; border-radius: var(--cssui-radius);}
   .logo-img { display:  flex; flex-direction: row-reverse; align-items:  center; }
   .logo-img img{ filter: grayscale(); width: 104px; height: 104px; transition: filter 0.4s ease-in-out;  }
   .logo-img:hover { cursor: pointer; }
   .logo-img:hover img { filter: none; }
   .subtitle::after {  position: absolute; content: ""; width: 0%; height: 4px; background-color: var(--white08); left: 50%; bottom: -10px; transition: all 0.4s ease-in-out;} 
   .logo-img:hover .subtitle::after { width: 100%; left:  0; }
   .subtitle { font-family: cy, sans-serif; font-size: 0.7rem; color: var(--white08); position: relative; padding: var(--cssui-padding);}

    /* *********//*/ Left top bar /*/
   .header-actions { width:  90vw; display: flex; flex-flow: row wrap; align-content: flex-start; justify-content: space-between; gap: 32px; position: fixed; z-index: 50000; top: 2%; left: 2%; }

    /* *********//*/ Audio Player /*/
   .header-sound { display: flex; flex-flow: row nowrap; align-items: flex-start; gap: 24px; border: 1px solid var(--primary-blue04); background: var(--black04); border-radius: var(--cssui-radius); padding: var(--cssui-padding); }

    /* *********//*/ Themes /*/
   details {  display:  flex; flex-direction: column; align-items: flex-end; position: fixed; z-index: 50000; top: 2%; right: 25%; border: 1px solid var(--primary-blue04); background: var(--black04); border-radius: var(--cssui-radius); padding: var(--cssui-padding); margin: 16px;  }
   .header-menu { border: 1px solid var(--primary-blue04); background: var(--black04); border-radius: var(--cssui-radius); padding: var(--cssui-padding);  }
   .icon { padding: var(--cssui-padding); margin: 8px 8px 0 4px; border-radius: var(--cssui-radius); border:  1px solid var(--black1);}
   .icon:hover { visibility: visible; background: var(--black02);}
   /* *********//*/ Theme Icons + Theme Container  /*/
   #dialog { position: fixed; z-index: 50000; top: 23%; right: 23%; }

   /* *********//*/ TEXT  /*/
   .headers { display: flex; flex-flow: row nowrap; user-select: none; border-radius: var(--cssui-radius); padding: var(--cssui-padding); position: fixed;left: 1%; bottom: 1%; z-index: 50000;}
   .main-title { color: var(--white08); padding: var(--cssui-padding); text-align: left; border-radius: var(--cssui-radius); }
   .main-subtitle { padding: var(--cssui-padding); background: var(--primary-red04); color: var(--white); }

 }

